.login-wrapper {
  overflow: hidden;
}
.login-header {
  color: #333;
  font-weight: 600;
  font-size: 1.28rem;
  line-height: 1.2;
  text-align: left;
  background: #e1e1e1;
  border-radius: 10px 10px 0 0;
  padding: 1.57rem 3.14rem;
}

.login-body {
  padding: 1.57rem 3.14rem;
  background: #f5f5f5;
  color: #333;
  border-radius: 0 0 5px 5px;
}

.login-form input {
  width: 100%;
  padding: 0.5em 1em;
  margin: 0.7em 0;
  border-radius: 5px;
  border: 0.4px solid #333;
  font-size: 1em;
  font-weight: 100;
  line-height: 1.57em;
}

.login-button {
  background: #82b641;
  padding: 0.5em 4.2em;
  color: #fff;
  text-decoration: none;
  line-height: 1.5em;
  border-radius: 5px;
  border: 0;
  outline: 0;
  margin-top: 0.8em;
  transition: all .3s ease;
  cursor: pointer;
  &:hover {
    background-color: rgb(36, 119, 36);
  }
}

.login-forgot {
  padding: 1em 0;
}

.login-forgot a {
  color: rgb(30, 152, 252);
  &:hover {
    text-decoration: underline;
  }
}

.other-login-method {
  display: flex;
  font-size: 0.85em;
  justify-content: space-between;
  margin-top: 1em;
  line-height: 2.5em;
}

.website-brand {
  width: 8rem;
  height: 2.2rem;
  margin-left: 2em;
  display: block;
  cursor: pointer;
}

.new-user {
  display: flex;
  margin: 2em 0;
  justify-content: space-around;
  line-height: 5.7em;
  border-radius: 7px;
  font-size: 0.85em;
  background-color: rgba(103, 167, 1, 0.11);
  height: 6em;
  font-weight: 600;
}

.new-user button {
  background: #82b641;
  padding: 0.5em 4em;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
  height: 2.6em;
  margin-top: 1.6em;
  border: 0;
  outline: 0;
  transition: all .3s ease;
  cursor: pointer;
  &:hover {
    background-color: rgb(36, 119, 36);
  }
}

.facebook-login {
  display: flex;
  width: 85%;
  margin: 0 auto;
  font-size: 0.9em;
  color: rgb(146, 146, 146);
  justify-content: center;
}

.facebook-login a {
  color: rgb(145, 145, 145);
  text-decoration: underline;
}
